import React from 'react'
import { makeETarget } from '../../Tools/codeTools'
import { stopAudio } from './Audio_Functions/Audio_Global_Functions.js'
import voiceRecordingOne from '../../../Assets/Sounds/Talking/test-sound-five.mp3'
import voiceRecordingTwo from '../../../Assets/Sounds/Talking/test-sound-six.mp3'
import voiceRecordingThree from '../../../Assets/Sounds/Talking/test-sound-seven.mp3'
import voiceRecordingFour from '../../../Assets/Sounds/Talking/test-sound-eight.mp3'
import musicRecordingOne from '../../../Assets/Sounds/Music/test-sound-one.mp3'
import musicRecordingTwo from '../../../Assets/Sounds/Music/test-sound-two.mp3'
import musicRecordingThree from '../../../Assets/Sounds/Music/test-sound-three.mp3'
import musicRecordingFour from '../../../Assets/Sounds/Music/test-sound-four.mp3'
import {faPlay,faStop} from '@fortawesome/free-solid-svg-icons'
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { PlayStopBtn } from "./Signal_Generator/Play_Stop.js"
import { mstrAudioNode } from './Audio_Tool'
//import { musicRecordingFour } from '../../../Assets/Sounds/Music/Music_Four.mp3'

const playIcon = <FontAwesomeIcon icon={faPlay} size="3x" color="black" style={{padding: "4.5px"}}/>
const stopIcon = <FontAwesomeIcon icon={faStop} size="3x" color="black" style={{padding: "4.5px"}}/>

class RecordingPlayBack extends React.Component {
    constructor(props){
        super(props);
        this.state = { 
            type: "rpb"
        }
    }
    componentDidMount(){
        rpbConfig.AXSource = document.querySelector(".mstr-audio-node");
    }

    render() {
        //make function that returns correct audio source for specific audio lmnt (used in mapped html)
        function populateRpb(index,type){
            switch(index){
                case 0:
                    if(type == "voice"){
                        return voiceRecordingOne;
                    }
                    else if (type == "music"){
                        return musicRecordingOne;
                    }
                case 1:
                    if(type == "voice"){
                        return voiceRecordingTwo;
                    }
                    else if (type == "music"){
                        return musicRecordingTwo;
                    }
                case 2: 
                    if(type == "voice"){
                        return voiceRecordingThree;
                    }
                    else if (type == "music"){
                        return musicRecordingThree;
                    }
                case 3: 
                    if(type == "voice"){
                        return voiceRecordingFour;
                    }
                    else if (type == "music"){
                        return musicRecordingFour;
                    }
            }
        }
      return (
        <div>
              {/* Voice play back */}
              <div className="audio-rpb-wrap tool-element-wrap display-none" aria-controls="Recording playback buttons">
                <div className="audio-controls-wrap human-voice">
                    <h2 className="rpb-header audio-header human-voice">Human Voice</h2>
                    <div className="audio-banner-wrap human-voice rpb">
                        {[1,2,3,4].map( (btn, index) => (
                            <div className="btn-rpb-wrap">
                                <div className="rpb icon-wrap wrap voice off" data-number={index} onClick={(e) => rpbFunctionality(e)}>
                                    <label className="rpb icon-label" type="text" for={`hv-rpb-btn ${index+1}`} style={{fontSize: "1.5em"}}>{index+1}</label>
                                    <li className="human-voice btn-pb" id={`hv-rpb-btn ${index+1}`}> 
                                        <span class="on rpb-icon play">{playIcon}</span> 
                                        <span class="off rpb-icon stop">{stopIcon}</span> 
                                    </li>
                                    <audio src={populateRpb(index,"voice")} className="rpb-audio human-voice" loop></audio>
                                </div>
                            </div>
                        ))}
                    </div>
                </div>
                {/* Music play back */}
                <div className="audio-controls-wrap music">
                    <h2 className="rpb-header audio-header music" style={{marginTop: "70px"}}>Music</h2>
                    <div className="audio-banner-wrap music rpb" style={{marginBottom: "5   0px"}}>
                        {[1,2,3,4].map( (btn, index) => (
                            <div className="btn-rpb-wrap">
                                <div className="rpb icon-wrap wrap music off" data-number={index} onClick={(e) => rpbFunctionality(e)}>
                                    <label className="rpb icon-label" type="text" for={`music-rpb-btn ${index+1}`} style={{fontSize: "1.5em"}}>
                                        {index+1}
                                    </label>
                                    <li className="human-voice btn-pb" id={`music-rpb-btn${index+1}`}>
                                        <span class="on rpb-icon play">{playIcon}</span> 
                                        <span class="off rpb-icon stop">{stopIcon}</span> 
                                    </li>
                                    <audio src={populateRpb(index,"music")} className="rpb-audio music" loop></audio>
                                </div>
                            </div>
                        ))}
                    </div>
                </div>
              </div>
        </div>
      );
    }
  }


//LOCAL FUNCTIONS AND VARIABLES 
console.log("master audio node is " + document.querySelector(".mstr-audio-node"))
var rpbConfig = {
    state: "stopped",
    AXSource: document.querySelector(".mstr-audio-node"),
}


//return audio of specific rpb audio (eg if 2nd music btn is clicked, return value of 2nd music audio)
function makeAxTarget(eTarget){ 
   
    var result = '';
    switch(eTarget.dataset.number){
        case "0": result = (eTarget.classList.contains("voice")) ? voiceRecordingOne : musicRecordingOne; break;
        case "1": result = (eTarget.classList.contains("voice")) ? voiceRecordingTwo : musicRecordingTwo; break;
        case "2": result = (eTarget.classList.contains("voice")) ? voiceRecordingThree : musicRecordingThree; break;
        case "3": result = (eTarget.classList.contains("voice")) ? voiceRecordingFour : musicRecordingFour; break;
    }
    return result;
}

function toggleOnOffRPB(eTarget){
    // if(document.querySelector(".icon-wrap.on" != null)){
    //     document.querySelector(".icon-wrap.on").classList.add("off"); document.querySelector(".icon-wrap.on").classList.remove("on");
    // } 
    
    if(eTarget.classList.contains("off")){
        eTarget.classList.add("on"); eTarget.classList.remove("off");
    } else{
        eTarget.classList.remove("on"); eTarget.classList.add("off");
    }
}

function playRpbAudio(eTarget) {// - play audio to clicked rpb source, update eTargets image and update rpbConfig
    
    var audioSource = makeAxTarget(eTarget);
    // //set AXSource to master Audio Elmnt
    // rpbConfig.AXSource = audioSource;
    //click a play button and the audio source within that click starts playing
    rpbConfig.AXSource.setAttribute("src",audioSource);
    rpbConfig.AXSource.load(); rpbConfig.AXSource.play();
    //change eTargets image to a pause button 
    eTarget.children[1].children[0].classList.remove("on"); eTarget.children[1].children[0].classList.add("off"); // hide play icon
    eTarget.children[1].children[1].classList.remove("off"); ["on","playing"].forEach( item =>  eTarget.children[1].children[1].classList.add(item) ); // display stop icon
    //add the current playing audio to rpbConfig (same as sgConfig but for rpb) - 
    
}

function changeAudio(eTarget) { //change from one playing source to another, update prev clicked image and update eTargets image

    //- change currently playing audios image to  "play button" && change "clicked" img to pause

        //dim the color of the previous playing audio wrap
        document.querySelector(".rpb.icon-wrap.wrap.on").classList.add("off");
        document.querySelector(".rpb.icon-wrap.wrap.on").classList.remove("on");

        //remove stop image to previos playing audio (add .off and remove .on)
        document.querySelector(".rpb-icon.playing").parentElement.children[1].classList.add("off");
        document.querySelector(".rpb-icon.playing").parentElement.children[1].classList.remove("on");

        //display play button to previous playing audio (add .off and remove .on to "playing" stop btn)
        document.querySelector(".rpb-icon.playing").parentElement.children[0].classList.add("on");
        document.querySelector(".rpb-icon.playing").parentElement.children[0].classList.remove("off");

        //remove .playing from previously playing audios stop btn
        ["playing"].forEach( item =>  document.querySelector(".rpb-icon.playing").classList.remove(item) ); 
        //document.querySelector(".rpb-icon.playing").classList.add("on");

        //populate stop image to "click" audio (removing .off and adding .on)
        eTarget.children[1].children[0].classList.remove("off"); eTarget.children[1].children[0].classList.add("on"); 

    stopAudio([rpbConfig.AXSource]) // - stop current sound
    playRpbAudio(eTarget) //- plays "clicked sound", change image, update rpbConfig
}

function stopRpbAudio(eTarget) { // stop audio to clicked rpb audio source and update eTargets image
    //click a play button and the audio source within that click stops
    stopAudio([rpbConfig.AXSource]) // - stop current sound
    //change eTargets image to a play button
    eTarget.children[1].children[0].classList.remove("off"); eTarget.children[1].children[0].classList.add("on"); //hide display icon
    eTarget.children[1].children[1].classList.remove("on"); eTarget.children[1].children[1].classList.add("off"); //add play icon 
    document.querySelector(".rpb-icon.playing").classList.remove("playing"); //remove .playing from previously stop button
}
//"CLICK" =>

function rpbFunctionality(e){
    //if recording playback mode is enabled
    if(document.querySelector(".audio-test-type-btn.rpb").classList.contains("on")){
        
        console.log("trigger play button")
        var eTarget = makeETarget(e);
        if (rpbConfig.state == "stopped"){ //initial (AUDIO NOT PLAYING => AUDIO PLAYING)
            toggleOnOffRPB(eTarget);
            playRpbAudio(eTarget);
            rpbConfig.state = "playing";
        }else if (rpbConfig.state == "playing" && eTarget.classList.contains("off")){ //(AUDIO PLAYING => NEW AUDIO SOURCE)
            changeAudio(eTarget);
            toggleOnOffRPB(eTarget);
        }else if (rpbConfig.state == "playing" && eTarget.classList.contains("on")){ //(AUDIO PLAYING => AUDIO STOPPED)
            toggleOnOffRPB(eTarget);
            stopRpbAudio(eTarget)
            rpbConfig.state = "stopped"
            //rpbConfig.AXSource = "standby"
        }
    }
}


  export { RecordingPlayBack,toggleOnOffRPB,stopRpbAudio,rpbConfig }


