import React from 'react'
import Constant from "../../../../Assets/Images/Audio_Sweep_Constant.png"
import HighToLow from "../../../../Assets/Images/Audio_Sweep_HighToLow.png"
import LowToHigh from "../../../../Assets/Images/Audio_Sweep_LowToHigh.png"
import PinkNoiseImg from "../../../../Assets/Images/Noise_Pink.png"
import WhiteNoiseImg from "../../../../Assets/Images/Noise_White.png"
import SineWaveImg from "../../../../Assets/Images/Sinewave_Transparent.png"
import { 
    sinewaveNodeArray, curAXSource, sgConfig, audioContext, destination, sineWaveLowsNode, 
    sineWaveMidsNode, sineWaveHighsNode, sineWaveHigh2LowNode, sineWaveLow2HighNode, whiteNoiseAXSrc, whiteNoiseNode,
    pinkNoiseAXSrc,pinkNoiseNode, lowPsFltr, midNtchFltr, highPsFltr, initSgConfing,pinkNoiseConstantSrc,whiteNoiseConstantSrc,whiteNoiseL2HSrc,
    whiteNoiseH2LSrc,pinkNoiseL2HSrc,pinkNoiseH2LSrc,sineWaveL2HSrc,sineWaveH2LSrc
} from '../Audio_Functions/Audio_Sg_Init'
import AX_pinkNoiseHigh2Low from "../../../../Assets/Sounds/Noise/Pink_Noise_High2Low.wav"
import AX_pinkNoiseLow2High from "../../../../Assets/Sounds/Noise/Pink_Noise_Low2High.wav"
import AX_whiteNoiseHigh2Low from "../../../../Assets/Sounds/Noise/White_Noise_High2Low.wav"
import AX_whiteNoiseLow2High from "../../../../Assets/Sounds/Noise/White_Noise_Low2High.wav"
import AX_sineWaveLow2High from "../../../../Assets/Sounds/Noise/Sine_Wave_Low2High.wav"
import AX_sineWaveHigh2Low from "../../../../Assets/Sounds/Noise/Sine_Wave_High2Low.wav"
import { makeETarget } from '../../../Tools/codeTools'
import { stopAudio } from '../Audio_Functions/Audio_Global_Functions'
import { isMasterConstant } from './Sources'


//******** REACT MARKUP *********** 
class AudioConstantSweep extends React.Component {
    constructor(props){
        super(props);
        this.state = { 
            type: "cos"
        }
    }
    componentDidMount(){
        initSgConfing(this.state.type);
        triggerMaster();
    } 
    render(){
        return(
            <div className="audio-controls-wrap constant-sweep">
                <h2 className="sg-header audio-header constant-sweep" >Constant / Sweep</h2>
                <div className="audio-banner-wrap cos constant-sweep wrap" data-type="constant"> {/* banner wrap (encasing icons) */}
                    {/* constant button */}
                    <div className="audio-control-btn cos constant on wrap">{/* wrapping both the img and label for tool option */}
                        <div class="audio-control-icon-wrap constant">
                            <img alt="Constant Full Spectrum" className="audio-control-icon constant cos" src={Constant}></img>
                        </div>
                        <label className="audio-control-label constant cos">Full Spectrum</label>
                    </div>
                    {/* high to low button */}
                    <div className="audio-control-btn cos high-to-low sweep off wrap" data-type="sweeping" data-name="high-to-low">{/* wrapping both the img and label for tool option */}
                        <div class="audio-control-icon-wrap high-to-low">
                            <img alt="High To Low" className="audio-control-icon high-to-low cos" src={HighToLow}></img>
                        </div>
                        <label className="audio-control-label high-to-low cos">High To Low</label>
                        <audio className="audio-source high-to-low sweep sine-wave" src={AX_sineWaveHigh2Low} loop></audio>
                        <audio className="audio-source high-to-low sweep pink-noise" src={AX_pinkNoiseHigh2Low} loop></audio>
                        <audio className="audio-source high-to-low sweep white-noise" src={AX_whiteNoiseHigh2Low} loop></audio>
                    </div>
                    {/* low-to-high button */}
                    <div className="audio-control-btn cos low-to-high sweep off wrap" data-type="sweeping" data-name="low-to-high" >{/*wrapping both the img and label for tool option*/}
                        <div class="audio-control-icon-wrap low-to-high">
                            <img alt="LowToHigh" className="audio-control-icon low-to-high cos" src={HighToLow}></img>
                        </div>
                        <label className="audio-control-label low-to-high cos">Low To High</label>
                        <audio className="audio-source low-to-high sweep sine-wave" src={AX_sineWaveLow2High} loop></audio>
                        <audio className="audio-source low-to-high sweep pink-noise" src={AX_pinkNoiseLow2High} loop></audio>
                        <audio className="audio-source low-to-high sweep white-noise" src={AX_whiteNoiseLow2High} loop></audio>
                    </div>
                </div>
            </div>
        )
    }
}

    function triggerMaster(e){
        console.log("trigger master")
        //play currently selected sound
        document.querySelectorAll(".audio-control-btn.cos").forEach( lmnt => lmnt.addEventListener("click", (e) => constantSweepFunct(e)) )
    }

    //****CONSTANT/SWEEP(switch between constant or sweeping signal)*****
        //********** 
        //NOTE:  sweep now only needs source.start() and source.stop()
        //RUN WHEN COS WRAP CLICKED
        // - this functionality is for when the audio is playing or stopped and cos btns clicked,
        // not for triggering the sound via play button (see play_stop.js)
        //and not for switching the source inputs (see sources.js)

        var sgcConstantSC = ''; //used to capture the settings of constant mode when sweep is enabled
        var constantFrequencies = '';
        var constantSweepFunct = "";

            constantSweepFunct = function(e){
                //if constant on and sweeping btn clicked, inital setup, disable bottom btn filters and stop audio from playing using stopAudio();
                    //remove .off add [.on,.sweep-enabled] to cos clicked element, update sgConfig, add .off remove .on to frequency wrap 
                    var eTarget = makeETarget(e);

                    
                    //toggle sweeping buttons (turn on "clicked" and turn off newly non clicked)
                    document.querySelector(".cos.on").classList.add("off"); document.querySelector(".cos.on").classList.remove("on"); 
                    eTarget.classList.add("on"); eTarget.classList.remove("off");
                    console.log("working")
    
                    // either turn sweeping mode on, switching between sweeping modes or turning sweeping mode off
                    switch(sgConfig.character){
    
                    //"CLICK" CONSTANT -> SWEEP - disable and turn off filter btns and play relevant sweeping sound
                    case "constant": // 
    
                        //turn off filter btns 
                        document.querySelectorAll(".audio-control-btn.frequency").forEach(filter => filter.classList.add("disabled") );
    
                        if(sgConfig.state == "playing"){ //if sound currently playing
                            //turn off current audio
                            (sgConfig.AXSource[0] == "sine wave") ? stopAudio(sgConfig.AXSource[1]) : stopAudio([sgConfig.AXSource[1]]);    
    
                            //turn on "clicked" audio 
                            //playCosSounds(e);
                            switchSweeps(e); sgConfig.AXSource[1].load(); sgConfig.AXSource[1].play();
                        }
                        //set the sound character in sgConfig.character
                        sgConfig.character = eTarget.dataset.type; //either high-to-low or low-to-high
                        
                        //save snapshop of SgConfig constant settings
                        //sgcConstantSC = [sgConfig].slice(0); 
                        constantFrequencies = [sgConfig.frequency].slice(0);
                        sgConfig.frequency = "frequencies disabled";

                        //populate relative sweep sound into sgConfig
                        if(flunk == 1)switchSweeps(e) //if flunk is equal to 1, it hasnt already been called, if its equal to 2, it has already been played
                    break;
    
                    //"CLICK" SWEEP -> SWEEP / SWEEP -> CONSTANT - switch back to saved constant settings or change cos source
                    case "sweeping": 
                        //COS -> COS - if previously clicked was sweeping and newly clicked is sweeping - switch sweeping sources
                        if (eTarget.classList.contains("sweep")){

                            if(sgConfig.state == "playing"){ //if sound currently playing
                                //change sweeping sources - turn off orginal and turn on "clicked"
                                stopAudio([sgConfig.AXSource[1]]); switchSweeps(e); sgConfig.AXSource[1].load(); sgConfig.AXSource[1].play();
                            }

                            //populate relative sweep sound into sgConfig, if switchsweeps hasnt already been ran before
                            if(flunk == 1)switchSweeps(e)

                            //update sgConfig
                            sgConfig.character = eTarget.dataset.type; 
                        }
                        
                        // SWEEP -> CONSTANT - if previous clicked was sweeping and newly clicked is constnat - turn off sweeping source, change sgConfig to snapshot and play constant sound
                        if (eTarget.classList.contains("constant")){
    
                            //re-enable filters by removing .disabled on filter btns
                            document.querySelectorAll(".audio-control-btn.frequency").forEach(filter => filter.classList.remove("disabled") );
    
                            //if sound playing, turn off current sound 
                            if(sgConfig.state == "playing"){
                                stopAudio([sgConfig.AXSource[1]]); //playCosSounds(e); //stop cos audio
                                // (sgConfig.AXSource[0] == "sine wave") ? sgConfig.AXSource[1].forEach( item => item.play()) : sgConfig.AXSource[1].play() //play constant sound
                            }
    
                            //update sgConfig
                            sgConfig.frequency = constantFrequencies; // revert sgConfig back to the setting it was on when it last constant 
                            sgConfig.character = "constant"; 

                            //play relative constant sound if neccesary and also capture it in sgConfig
                            if(sgConfig.AXSource[0] == "pink noise"){
                                document.querySelector(".mstr-audio-node").setAttribute("src",pinkNoiseConstantSrc);
                                if(sgConfig.state == "playing") {
                                    sgConfig.AXSource[1].load(); sgConfig.AXSource[1].play();
                                }//OLD: document.querySelector(".audio-source.pink-noise.constant").play();
                                //OLD:sgConfig.AXSource[1] = document.querySelector(".audio-source.pink-noise.constant");
                            }
                            else if(sgConfig.AXSource[0] == "white noise"){
                                document.querySelector(".mstr-audio-node").setAttribute("src",whiteNoiseConstantSrc);
                                if(sgConfig.state == "playing") {
                                    sgConfig.AXSource[1].load(); sgConfig.AXSource[1].play();
                                }
                                //OLD sgConfig.AXSource[1] = document.querySelector(".audio-source.white-noise.constant");
                            }                 
                            else if(sgConfig.AXSource[0] == "sine wave"){
                                if(sgConfig.state == "playing") { sinewaveNodeArray.forEach( item => item.play()); }
                                sgConfig.AXSource[1] = sinewaveNodeArray;
                            }
                            //run new idea code
                        } break;
                    } 

            }    


        var flunk = 1; //used so switchSweeps is only called once for above function
        var flunker = () => setTimeout(() => {
            flunk--;
        }, 500);

            function switchSweeps(e){
                flunk++ 
                flunker(); //to prevent switchSweeps from being called twice as 
                var eTarget = makeETarget(e);
                //populate relative sweep sound into sgConfig
                if(sgConfig.AXSource[0] == "pink noise" ){
                    //if(sgConfig.state == "playing") document.querySelector(".audio-source.pink-noise.constant").play();
                    if (eTarget.classList.contains("high-to-low")){ sgConfig.AXSource[1].setAttribute("src",pinkNoiseH2LSrc)}
                    else if (eTarget.classList.contains("low-to-high")){ sgConfig.AXSource[1].setAttribute("src",pinkNoiseL2HSrc)} 
                }
                else if(sgConfig.AXSource[0] == "white noise"){ 
                    if (eTarget.classList.contains("high-to-low")){ sgConfig.AXSource[1].setAttribute("src",whiteNoiseH2LSrc)} 
                    else if (eTarget.classList.contains("low-to-high")){ sgConfig.AXSource[1].setAttribute("src",whiteNoiseL2HSrc)} 
                }                 
                else if(sgConfig.AXSource[0] == "sine wave"){
                    // double check that the audio being played is the master audio element and not any of the 3 indiviual sine wave nodes
                    if (sgConfig.AXSource[1][0] != undefined) { sgConfig.AXSource[1] = document.querySelector(".mstr-audio-node"); } 
                    if (eTarget.classList.contains("high-to-low")){ sgConfig.AXSource[1].setAttribute("src",sineWaveH2LSrc)} 
                    else if (eTarget.classList.contains("low-to-high")){ sgConfig.AXSource[1].setAttribute("src",sineWaveL2HSrc)} 
                }
            }
export {AudioConstantSweep}