import React from 'react'
import { makeETarget } from '../../Tools/codeTools'
import { sgConfig } from './Audio_Functions/Audio_Sg_Init';
import { stopAudio,tglSgPlayBtn, forceRpbOff } from './Audio_Functions/Audio_Global_Functions.js'
import { rpbConfig } from './Audio_Rpb'
import { clickOutAudio,ClassWatcher,classWatcher,workOnClassRemoval,workOnClassAdd, } from './clickOutAudio.js'

class Btn_Toggle extends React.Component {
    constructor(props){
        super(props);
        this.state = {
            type: "btn_toggle"
         }
    }

    componentDidMount(){
        clickOutAudio(); //watch for when user exits page, so then to stop any audio that may be playing
    }

    render() {

      return (
        <div className="audio-test-type-wrap sg-on">
            <button className="audio-test-type-btn sg on wrap" onClick={ (e) => btnTgl(e) } style={{marginLeft: "25px"}}><span>Signal Generator</span></button>
            <button className="audio-test-type-btn rpb off wrap" onClick={ (e) => btnTgl(e) } style={{marginRight: "25px"}}><span>Recording Playback</span></button>
        </div>
      );
    }
  }

  var sgLastSound = '';
  function btnTgl(e){
    const rpbWrap = document.querySelector(".audio-rpb-wrap.tool-element-wrap");
    const sgWrap = document.querySelector(".audio-sg-wrap.tool-element-wrap")
    const playBtn = document.querySelector(".play-stop-btn");

    var eTarget = makeETarget(e);

    //if an audio toggle button is clicked, stop audio if audio playing (remember to add smarts to decipher whether or not constant sinewave is present)
    (sgConfig.AXSource[1][0] != undefined) ? stopAudio(sgConfig.AXSource[1]) : stopAudio([sgConfig.AXSource[1]]);

    //"click" either sg btn or tpb btn
    if(eTarget.classList.contains("sg") && eTarget.classList.contains("off")){ // "click" "signal generator" btn

      //turn sg btn on, rpb btn off and remove displayNone from sg wrap and add displayNone to rpb wrap
      document.querySelector(".audio-sg-wrap.tool-element-wrap").classList.remove("display-none")
      document.querySelector(".audio-rpb-wrap.tool-element-wrap").classList.add("display-none")
      playBtn.classList.remove("display-none")
      // [playBtn,sgWrap].forEach(item => {
      //     item.classList.remove("display-none")
      // })

      //remove .on from audio-test-type-btn and add .off  - then turn sg btn on
      document.querySelector(".audio-test-type-btn.on").classList.add("off"); document.querySelector(".audio-test-type-btn.on").classList.remove("on")
      eTarget.classList.add("on"); eTarget.classList.remove("off");
      document.querySelector(".mstr-audio-node").classList.remove("rpb-on");
      document.querySelector(".mstr-audio-node").classList.add("sg-on");

      //populate previously play signal back into master audio element 
      document.querySelector(".mstr-audio-node").setAttribute("src",sgLastSound);

      //stop rpb audio and change relevant classes 
      if(rpbConfig.state == "playing"){ forceRpbOff(); }

    }
    else if(eTarget.classList.contains("rpb") && eTarget.classList.contains("off")){ // "click" "recording playback" btn

        //store last played sg sound in a variale to populate later if user goes back to sg mode
        sgLastSound = [sgConfig.AXSource[1]].slice(-1)[0].src; 

        //turn rpb btn on, sg btn off and remove displayNone from rpb wrap and add displayNone to rpb wrap
        document.querySelector(".audio-rpb-wrap.tool-element-wrap").classList.remove("display-none")
        document.querySelector(".audio-sg-wrap.tool-element-wrap").classList.add("display-none")
        playBtn.classList.add("display-none");
        // [playBtn,sgWrap].forEach(item => {
        //     item.classList.add("display-none")
        // })

        //remove .on from audio-test-type-btn and add .off  - then turn rpb btn on
        document.querySelector(".audio-test-type-btn.on").classList.add("off"); document.querySelector(".audio-test-type-btn.on").classList.remove("on")
        eTarget.classList.add("on"); eTarget.classList.remove("off");

        //tell masterAudio element what sound type is active
        document.querySelector(".mstr-audio-node").classList.add("rpb-on");
        document.querySelector(".mstr-audio-node").classList.remove("sg-on");

        //turn play btn off and change relevant classes 
        if(sgConfig.state == "playing"){ tglSgPlayBtn("stop"); }
    }
  }

  export {Btn_Toggle}

