import React from 'react'
//import sound stuff
import { 
    sinewaveNodeArray, sgConfig, sineWaveLowsNode, 
    sineWaveMidsNode, sineWaveHighsNode, sineWaveHigh2LowNode, sineWaveLow2HighNode, whiteNoiseNode,
    pinkNoiseAxSrc,pinkNoiseNode, initSgConfing,pinkNoiseConstantSrc,whiteNoiseConstantSrc,whiteNoiseL2HSrc,
    whiteNoiseH2LSrc,pinkNoiseL2HSrc,pinkNoiseH2LSrc,sineWaveL2HSrc,sineWaveH2LSrc
} from '../Audio_Functions/Audio_Sg_Init'
import { stopAudio} from '../Audio_Functions/Audio_Global_Functions.js'
import {mstrAudioNode} from '../Audio_Tool.js'
//import images
import PinkNoiseImg from "../../../../Assets/Images/Noise_Pink.png"
import WhiteNoiseImg from "../../../../Assets/Images/Noise_White.png"
import SineWaveImg from "../../../../Assets/Images/Sinewave_Transparent.png"
import AX_sineWaveConstantHigh from "../../../../Assets/Sounds/Noise/Sine_Wave_High.wav"
import AX_sineWaveConstantMid from "../../../../Assets/Sounds/Noise/Sine_Wave_Mid.wav"
import AX_sineWaveConstantLow from "../../../../Assets/Sounds/Noise/Sine_Wave_Low.wav"
import AX_pinkNoiseConstant from "../../../../Assets/Sounds/Noise/pink-noise-tone.mp3"
import AX_whiteNoiseConstant from "../../../../Assets/Sounds/Noise/white-noise-tone.mp3"

//****EQ/FILTER/FREQUENCY (attenuate/deattenuate relevant frequency)*****

//******** REACT MARKUP *********** 
class AudioSources extends React.Component {
    constructor(props){
        super(props);
        this.state = { 
            type: "sources"
        }
    }
    componentDidMount(){
        initSgConfing(this.state.type);
    } 
    render(){
        
        return(
            <div>
                <div className="audio-controls-wrap noise-type">
                    <h2 className="sg-header audio-header noise-type">Source</h2>
                    <div className="audio-banner-wrap noise-type"> {/* banner wrap (encasing icons) */}
                        <div className="audio-control-btn pink-noise off source wrap" data-source="pink noise" onClick={(e) => switchSound(e)}>{/* wrapping both the img and label for tool option */}
                            <img alt="Pink Noise" className="audio-control-icon pink-noise source" src={PinkNoiseImg}></img>
                            <label className="audio-control-label pink-noise noise">Pink Noise</label>
                            <audio className="audio-source pink-noise constant noise" src={AX_pinkNoiseConstant} loop></audio>
                        </div>
                        <div className="audio-control-btn white-noise on source wrap" data-source="white noise" onClick={(e) => switchSound(e)}>{/* wrapping both the img and label for tool option */}
                            <img alt="White Noise" className="audio-control-icon white-noise source" src={WhiteNoiseImg}></img>
                            <label className="audio-control-label white-noise noise">White Noise</label>
                            <audio className="audio-source white-noise constant noise" src={AX_whiteNoiseConstant} loop></audio>
                        </div>
                        <div className="audio-control-btn sinewave off source wrap" data-source="sine wave" onClick={(e) => switchSound(e)}>{/* wrapping both the img and label for tool option */}
                            <img alt="Sine Wave" className="audio-control-icon sinewave source" src={SineWaveImg}></img>
                            <label className="audio-control-label sinewave">Sine Wave</label>
                            <audio className="audio-source sine-wave-high constant sine" src={AX_sineWaveConstantHigh} loop></audio>
                            <audio className="audio-source sine-wave-mid constant sine" src={AX_sineWaveConstantMid} loop></audio>
                            <audio className="audio-source sine-wave-low constant sine" src={AX_sineWaveConstantLow} loop></audio>
                        </div>
                    </div>
                </div>
            </div>
        )
    }
}

export {AudioSources}

function switchSound(e){
    
    var eTarget = '';
    eTarget = (() => {
        if (e.target.classList.contains("wrap")){ return e.target }
        else if (e.target.parentElement.classList.contains("wrap")){ return e.target.parentElement }
    })();

//set sgConfig.AXSource[1] to master Audio node if it isnt and needs it
function isMasterConstant(){ 
   
    if(/*sgConfig.AXSource[1].length > 1*/sgConfig.AXSource[1][0] != undefined){
        sgConfig.AXSource[1] = document.querySelector(".mstr-audio-node");
    }
}
    //****AUDIO SOURCE (choose noise source)*****
        //********** 
            if (eTarget.classList.contains("off") && eTarget.classList.contains("source")){

                var elmntSourceArray = document.querySelector(".audio-control-btn.source");
                isMasterConstant(); //set sgConfig.AXSource[1] to master Audio node if it isnt and needs it

                        //if sound(s) are playing, stop the sound(s) (then play the "clicked" sound(s) later after next code execution
                        if(sgConfig.state == "playing"){
                            switch(sgConfig.AXSource[0]){
                                case "pink noise":
                                    stopAudio([sgConfig.AXSource[1]]); 
                                    break;
                                case "white noise":
                                    stopAudio([sgConfig.AXSource[1]]); 
                                    break;
                                case "sine wave":
                                    if(sgConfig.character == "constant") { stopAudio(sinewaveNodeArray); }
                                    else if (sgConfig.character == "sweeping") { stopAudio([sgConfig.AXSource[1]]); }
                                    break;
                            }
                        }

                        //TURN PRV BTN OFF AND NEW BTN ON, SWITCH SOURCE TO CLICKED SOURCE AND TURN OFF OLD SOURCE
                        if (eTarget.classList.contains("off")){
                            document.querySelector(".audio-control-btn.source.on").classList.add("off");
                            document.querySelector(".audio-control-btn.source.on").classList.remove("on");
                            eTarget.classList.add("on");
                            eTarget.classList.remove("off");
                        }

                        //configure sgConfig to updated source && //change masterAudioSourceNode depending on what is selected
                        switch(eTarget.dataset.source){
                            case "pink noise":
                                sgConfig.AXSource[0] = "pink noise";
                                //sgConfig.AXSource[1] = mstrAudioNode;

                                if (sgConfig.character == "constant") { 
                                    sgConfig.AXSource[1].setAttribute("src",pinkNoiseConstantSrc); //OLD: sgConfig.AXSource[1] = pinkNoiseNode; 
                                }
                                else if (sgConfig.character == "sweeping" && document.querySelector(".cos.high-to-low").classList.contains("on")) { 
                                    sgConfig.AXSource[1].setAttribute("src",pinkNoiseH2LSrc); // OLD: sgConfig.AXSource[1] = document.querySelector(".high-to-low.pink-noise"); 
                                }
                                else if (sgConfig.character == "sweeping" && document.querySelector(".cos.low-to-high").classList.contains("on")) {
                                    sgConfig.AXSource[1].setAttribute("src",pinkNoiseL2HSrc);  // OLD: sgConfig.AXSource[1] = document.querySelector(".low-to-high.white-noise"); 
                                }
                                break;
                            case "white noise":
                                sgConfig.AXSource[0] = "white noise";

                                if (sgConfig.character == "constant") {
                                    sgConfig.AXSource[1].setAttribute("src",whiteNoiseConstantSrc); // OLD sgConfig.AXSource[1] = whiteNoiseNode; 
                                }
                                else if (sgConfig.character == "sweeping" && document.querySelector(".cos.high-to-low").classList.contains("on")) { 
                                    sgConfig.AXSource[1].setAttribute("src",whiteNoiseH2LSrc) // OLD sgConfig.AXSource[1] = document.querySelector(".high-to-low.white-noise");
                                }
                                else if (sgConfig.character == "sweeping" && document.querySelector(".cos.low-to-high").classList.contains("on")) { 
                                    sgConfig.AXSource[1].setAttribute("src",whiteNoiseL2HSrc)// OLD sgConfig.AXSource[1] = document.querySelector(".low-to-high.white-noise"); 
                                }
                                break;
                            case "sine wave":
                                sgConfig.AXSource[0] = "sine wave";
                                if (sgConfig.character == "constant") { sgConfig.AXSource[1] = sinewaveNodeArray; }
                                else if (sgConfig.character == "sweeping" && document.querySelector(".cos.high-to-low").classList.contains("on")) {
                                    sgConfig.AXSource[1].setAttribute("src",sineWaveH2LSrc); //OLD: sgConfig.AXSource[1] = document.querySelector(".high-to-low.sine-wave");
                                }
                                else if (sgConfig.character == "sweeping" && document.querySelector(".cos.low-to-high").classList.contains("on")) { 
                                    sgConfig.AXSource[1].setAttribute("src",sineWaveL2HSrc); // OLDsgConfig.AXSource[1] = document.querySelector(".low-to-high.sine-wave"); 
                                }
                                break;
                        }

                        //turn sound on if sound was switched while playing
                        if(sgConfig.state == "playing"){
                            switch(eTarget.dataset.source){
                                case "pink noise":
                                    //[sgConfig.AXSource[1]].forEach( item => item.play() ); 
                                    sgConfig.AXSource[1].load();
                                    sgConfig.AXSource[1].play();
                                case "white noise":
                                    //[sgConfig.AXSource[1]].forEach( item => item.play() ); 
                                    //sgConfig.AXSource[1].load();
                                    sgConfig.AXSource[1].play();
                                    break;
                                case "sine wave":
                                    if (sgConfig.character == "constant") { sinewaveNodeArray.forEach( item => item.play() ); }
                                    else if (sgConfig.character == "sweeping") { 
                                        sgConfig.AXSource[1].load();
                                        sgConfig.AXSource[1].play(); 
                                    }
                                    break;
                            }
                        }
                }
}