//GLOBAL FUNCTIONS 

import { makeETarget } from "../../../Tools/codeTools";
import { mstrAudioNode } from "../Audio_Tool";
import { sgConfig, sinewaveNodeArray } from "./Audio_Sg_Init";
import { rpbConfig } from '../Audio_Rpb'

    //stop current source audio  (uses an array)
    function stopAudio(sound){
        //parse in sgConfig.curAxSource[1]
        sound.forEach( item => {
            item.pause();
            item.currentTime = 0;
        });
    }

    //Turn on / off Audio signal generator buttons - if state == stop, turn off and vice versa  (config, styles etc)
    function tglSgPlayBtn(state){
        var playStopBtn = document.querySelector(".play-stop-btn");
        if(state == "stop"){ // turn play button off, add "play" and change relevant classes
            sgConfig.state = "stopped";
            playStopBtn.classList.add("off"); playStopBtn.classList.remove("on");
            document.querySelector(".play-stop-btn").children[0].innerText = "PLAY";
        }
        else if (state == "play"){ // turn play button on, add "stop" and change relevant classes
            sgConfig.state = "playing";
            playStopBtn.classList.add("on"); playStopBtn.classList.remove("off");
            document.querySelector(".play-stop-btn").children[0].innerText = "STOP"
        }
    }

    //turn on off rpb audio
    function forceRpbOff(){
        var onRpbWrap =  document.querySelector(".rpb.icon-wrap.wrap.on");

        onRpbWrap.children[1].children[0].classList.remove("off"); onRpbWrap.children[1].children[0].classList.add("on"); //hide stop icon
        onRpbWrap.children[1].children[1].classList.remove("on"); onRpbWrap.children[1].children[1].classList.add("off"); //add play icon 

        document.querySelector(".rpb-icon.playing").classList.remove("playing"); //remove .playing from previously stop button

        onRpbWrap.classList.add("off"); onRpbWrap.classList.remove("on"); // make previously playing box opacity dimmed

        rpbConfig.state = "stopped"; //update rpbConfig
    }


export {stopAudio,tglSgPlayBtn,forceRpbOff}