import { 
    sinewaveNodeArray, curAxSource, sgConfig, audioContext, destination, sineWaveLowsNode, 
    sineWaveMidsNode, sineWaveHighsNode, sineWaveHigh2LowNode, sineWaveLow2HighNode, whiteNoiseAxSrc, whiteNoiseNode,
    pinkNoiseAxSrc,pinkNoiseNode, lowPsFltr, midNtchFltr, highPsFltr, initSgConfing
} from '../Audio_Functions/Audio_Sg_Init'

//update sgconfig.frequency function (eg so turning ["mid", "high"] -> ["low","mid", "high"] when low turned .on)
        function updateFrqAry(frq,eTarg){
            //removing frequency from sgConfig.frequency
            if(eTarg.classList.contains("off")){
                frq = sgConfig.frequency.indexOf(frq)
                sgConfig.frequency.splice(frq,1)
            }
            //adding frequency to sgConfig.frequency
            else if (eTarg.classList.contains("on")){
                sgConfig.frequency.push(frq);
            }
        }

        //update array that holds the 3 sinewave audio nodes (low, mid, high)
        function updateSineWaveNodeArray(frequency,eTarg){
            switch (frequency) {
                
                case "high": 
                    //remove high node from sineWaveNodeArray if clicked filter off (so user has just turned that filter off)
                    if(eTarg.classList.contains("off")){
                        sinewaveNodeArray.splice(sinewaveNodeArray.indexOf(sineWaveHighsNode),1); 
                    }
                    //add high node to sineWaveNodeArray if clicked filter is on (so user has just turned that filter on)
                    else{ sinewaveNodeArray.push(sineWaveHighsNode); } 
                    break;

                case "mid":
                    //remove mid node from sineWaveNodeArray if clicked filter off (so user has just turned that filter off)
                    if(eTarg.classList.contains("off")){ 
                        sinewaveNodeArray.splice(sinewaveNodeArray.indexOf(sineWaveMidsNode),1); 
                    }
                    //add high node to sineWaveNodeArray if clicked filter is on (so user has just turned that filter on)
                    else{ sinewaveNodeArray.push(sineWaveMidsNode); }

                    break;

                case "low":
                    //remove mid node from sineWaveNodeArray if clicked filter off (so user has just turned that filter off)
                    if(eTarg.classList.contains("off")){
                        sinewaveNodeArray.splice(sinewaveNodeArray.indexOf(sineWaveLowsNode),1); 
                    }
                    //add high node to sineWaveNodeArray if clicked filter is on (so user has just turned that filter on)
                    else{ sinewaveNodeArray.push(sineWaveLowsNode); }
                    
                    break;
                default: break;       
            }
        }

export {updateFrqAry,updateSineWaveNodeArray}