import React from 'react'
import LowFrequency from "./../../../../Assets/Images/Low_Freq_Transparent.png"
import MidFrequency from "./../../../../Assets/Images/Mid_Freq_Transparent.png"
import HighFrequency from "./../../../../Assets/Images/High_Freq_Transparent.png"
import {stopAudio} from '../Audio_Functions/Audio_Global_Functions.js'
import { 
    sinewaveNodeArray, curAxSource, sgConfig, audioContext, destination, sineWaveLowsNode, 
    sineWaveMidsNode, sineWaveHighsNode, sineWaveHigh2LowNode, sineWaveLow2HighNode, whiteNoiseAxSrc, whiteNoiseNode,
    pinkNoiseAxSrc,pinkNoiseNode, lowPsFltr, midNtchFltr, highPsFltr, initSgConfing
} from '../Audio_Functions/Audio_Sg_Init'
import {updateFrqAry, updateSineWaveNodeArray} from './FiltersJsFunctions'


//****EQ/FILTER/FREQUENCY (attenuate/deattenuate relevant frequency)*****

//******** REACT MARKUP *********** 
    class AudioFilters extends React.Component {
        constructor(props){
            super(props);
            this.state = { 
                type: "filters"
            }
        }
        
        componentDidMount(){
            initSgConfing(this.state.type);
            thisFunctionality();
        }
        render() {
            return (
                /* Frequency (only available with constant). Encasing all the elements for this section */
                <div className="audio-controls-wrap frequency">
                    <h2 className="sg-header audio-header frequency">Frequency</h2>
                    <div className="audio-banner-wrap low-frequency"> {/* banner wrap (encasing icons) */}
                        <div className="audio-control-btn low-frequency on frequency wrap" data-frequency="low">{/* wrapping both the img and label for tool option */}
                            <img alt="Low Frequency" className="audio-control-icon low-frequency frequency" src={LowFrequency}></img>
                            <label className="audio-control-label low-frequency frequency">Low Frequency</label>
                        </div>
                        <div className="audio-control-btn mid-frequency on frequency wrap" data-frequency="mid">{/* wrapping both the img and label for tool option */}
                            <img alt="White Noise" className="audio-control-icon mid-frequency frequency" src={MidFrequency}></img>
                            <label className="audio-control-label mid-frequency frequency">Mid Frequency</label>
                        </div>
                        <div className="audio-control-btn high-frequency on frequency wrap" data-frequency="high">{/* wrapping both the img and label for tool option */}
                            <img alt="High Frequency" className="audio-control-icon high-frequency frequency" src={HighFrequency}></img>
                            <label className="audio-control-label high-frequency frequency">High Frequency</label>
                        </div>
                    </div>
                </div>
            )
        }
    }

export {AudioFilters}


var prvFrqArray;
var prvntFunct = 1; // because multiple instances are fired off at once due to nature of fnctn being on multiple elements
function thisFunctionality(){

        //global variables
        var elmntFrqWrapArray = document.querySelectorAll(".frequency.wrap");
        var eTarget = '';
    
        //for loop through frequencies and add eventListener code for each
        for (var i = 0; i < elmntFrqWrapArray.length; i++){
                
                elmntFrqWrapArray[i].addEventListener("click", (e) => {
                    console.log(sgConfig.frequency);

                    prvntFunct++//to stop function running more than once at a time

                    //local variables
                    var onElmntFrqArray = document.querySelectorAll(".audio-control-btn.frequency.on");
                    var freqArray = '';

                    //make sure user is clicking in right area - note: 1 filter must always be on 
                    if (e.target.classList.contains("wrap") || e.target.parentElement.classList.contains("wrap")){ 

                        //make certain var eTarget is always the btn wrap
                            eTarget = (() => {
                                if (e.target.classList.contains("wrap")){ return e.target }
                                else if (e.target.parentElement.classList.contains("wrap")){ return e.target.parentElement }
                            })();
                            var frequency = eTarget.dataset.frequency //get "clicked" frequency
                            
                            //turn ".off" button by dimming brightness (unless it is the last filter on)
                            if(eTarget.classList.contains("on") && onElmntFrqArray.length > 1){
                                eTarget.classList.add("off");
                                eTarget.classList.remove("on");
                                //freqArray.splice(freqArray.indexOf(frequency),1); //eg output: [low,mid,high] -> [low,high]
                            }
                            //turn ".on" button by upping brightness
                            else if(eTarget.classList.contains("off")){
                                eTarget.classList.add("on");
                                eTarget.classList.remove("off");   
                                //freqArray.push(frequency); //eg output: [low,mid] -> [low,mid,high]
                            }
                            // UPDATE sgConfig.frequency - remove clicked frequency "string" from sgConfig.frequency, by checking e.targets data-set-frequency
                            updateFrqAry(frequency,eTarget); //
                            //update array that holds the sinewave audio nodes relevant to the filter state
                            updateSineWaveNodeArray(frequency,eTarget) 
                            freqArray = sgConfig.frequency//update freqArray variable to updated sgConfig.frequency

                        // push/splice relevant nodes from sinewaveNodeArray as eq filters are turned on and off, 
                        // as well as stop()/play() on relevant node if state is "playing " / "stoppped"
                        if (sgConfig.AXSource[0] == "sine wave" && prvFrqArray != freqArray){ 
                            switch (frequency) {
                                case "high": 
                                    if(sgConfig.state == "playing"){ //if audio is playing, relevant sine wave node
                                        if(eTarget.classList.contains("off")){ stopAudio([sineWaveHighsNode]); }
                                        else{ sineWaveHighsNode.play(); }
                                    } break;
        
                                case "mid":
                                    if(sgConfig.state == "playing"){ //if audio is playing, relevant sine wave node
                                        if(eTarget.classList.contains("off")){ stopAudio([sineWaveMidsNode]);}
                                        else{ sineWaveMidsNode.play(); }
                                    } break;
        
                                case "low":
                                    if(sgConfig.state == "playing"){ //if audio is playing, relevant sine wave node
                                        if(eTarget.classList.contains("off")){ stopAudio([sineWaveLowsNode]);}
                                        else{ sineWaveLowsNode.play(); }
                                    } break;
                                    
                                default: break;       
                            }
                        }
                        if(freqArray) prvFrqArray = freqArray.slice(0,freqArray.length); //incase function is trying to execute w/ same parameters as last time 
                    }
                }); 
        }
    }