var rngeCntr = 0;
function configRangeScript() {
  if(rngeCntr == 0 /*&& document.querySelector('.ui-slider-range')*/){
    debugger;
    var masterAudioNode = document.querySelector(".mstr-audio-node");
    
    //set volumes of audio nodes to 50%
        [masterAudioNode,sineWaveHighsNode,sineWaveMidsNode,sineWaveLowsNode].forEach( (node) => {
            node.volume = 1
        });

    //function that updates volume when volume is being interacted with
        var liveVolFunct = ''; 
        function updtVol(){
            const rngHndl = document.querySelector(".ui-slider-handle");
            debugger;
            liveVolFunct = setInterval(() => {
                var liveVolume = parseInt(rngHndl.style.left)/100;
                [masterAudioNode,sineWaveHighsNode,sineWaveMidsNode,sineWaveLowsNode].forEach( (node) => {
                    node.volume = liveVolume;
                }); 
            }, 20);
        }

    //Event listeners for updtVol
        //when audiorange handle is clicked, set the volume of all audio elements (eg .mstr-audio-node) as per the range handle
        const range = document.querySelector(".slider.green");

        //DESKTOP
        range.addEventListener("mousedown", (e) => {

            //as long as the user has their mouse down on rngHndl, update the volume
            updtVol();
        });
        //when users pulls mouse off range handle, stop updating volume
        range.addEventListener("mouseup",() => {
            clearInterval(liveVolFunct);
        });

        //RESPONSIVE
        range.addEventListener("touchstart", (e) => {
            
            //when user touches the range, update the volume accordingly
            updtVol();
        });
        //when users puls finger off range, stop updating volume
        range.addEventListener("touchend",() => {
            clearInterval(liveVolFunct);
        });
  }
}




